import React from "react";
import "../components/search.css";
import api from "../api";
import { connect } from "react-redux";
// import { read } from "../redux/userList";
import Search from "../components/Search";
import Insert from "../components/Insert";

class ReduxCrud extends React.Component {
  getUsers = () => {
    api
      .get("/api/user")
      .then((res) => {
        // console.log("   ");
        // console.log(res.data);
        this.props.setUsers(res.data);
      })
      .catch((err) => {
        alert("   ");
        console.log(err);
      });
  };
  render() {
    console.log("ReduxCrud render ======>>");
    const userList = this.props.userList;
    console.log(userList);
    if (userList.length !== 0) {
      return (
        <section>
          <Search userList={userList} reUserList={this.getUsers} />
          <hr></hr>
          <Insert reUserList={this.getUsers} />
        </section>
        // <section>
        //   <table>
        //     <thead>
        //       <tr>
        //         <th></th>
        //         <th></th>
        //         <th></th>
        //         <th> </th>
        //         <th></th>
        //       </tr>
        //     </thead>
        //     <tbody>
        //       {userList.map((user) => (
        //         <tr key={user.name}>
        //           <td>{user.name}</td>
        //           <td>{user.nickName}</td>
        //           <td>{user.phone}</td>
        //           <td>{user.place}</td>
        //           <td width="100px">
        //             <button></button>
        //             <button></button>
        //           </td>
        //         </tr>
        //       ))}
        //     </tbody>
        //   </table>
        // </section>
      );
    } else {
      console.log("");
      return <h2>Loding</h2>;
    }
  }
  componentDidMount() {
    this.getUsers();
  }
}

const mapReduxStateToReactProps = (state) => ({
  userList: state.userList.value,
});
const mapReduxDispatchToReactProps = (dispatch) => {
  return {
    setUsers: (value) => dispatch({ type: "userList/read", payload: value }),
    // 
    // getUsers: (value) => dispatch(read(value)),
  };
};

export default connect(
  //  state  props  
  //   ,     props .
  mapReduxStateToReactProps,
  //  Dispatch  props  
  mapReduxDispatchToReactProps
)(ReduxCrud);
