import { createSlice } from "@reduxjs/toolkit";
// import api from "../api";

export const userListSlice = createSlice({
  //  
  name: "userList",
  //   
  initialState: {
    value: [],
  },
  //      
  reducers: {
    read: (state, action) => {
      state.value = action.payload;
      /*
      api
        .get("/api/user")
        .then((res) => {
          state.value = res.data;
        })
        .catch((err) => {
          alert("   ");
          console.log(err);
        });
      */
    },
  },
});

export const { read } = userListSlice.actions;

export default userListSlice.reducer;
